.main-container {
  max-width: 100%;
  max-height: 100%;
  width:100%;
  height:100%;
  display:flex;
  flex-direction: column;
}

.bg-img {
  position:fixed;
  top:0;
  z-index:-1;
  width:100vw;
  height:100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
